
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.text {
  width: 90%;
  margin-right: 0px;
  padding: 0px;
}

#editor {
  width: 100%;
  height: 200px;
  padding-left: 0px;
  text-align: left;
}

#preview {
  width: 100%;
  height: fit-content;
  background-color: white;
  color: black;
  font-size: 11px;
  padding: 0px;
}
